nav {
  padding: 0.125rem;
  display: flex;
}
.nav-container {
  flex: 1;
  display: flex;
  justify-content: space-between;
  height: 100%;
}
.nav-center {
  flex: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1.5rem;
}
.nav-center > h2 {
  color: #fff;
  text-transform: capitalize;
  font-size: 1.8rem;
}
.nav-number-center {
  flex: 0 0 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: yellow;
  padding: 0.5rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.nav-number-img {
  color: black;
  /* font-size: 1.2rem; */
}
.nav-number-table {
  color: black;
  /* font-size: 1.2rem; */
}

@media screen and (max-width: 479px) {
  nav {
    flex: 0 0 90px;
    padding: 0.25rem;
    /* border: 1px solid red; */
  }
  .nav-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.15rem;
    /* border: 1px solid red; */
  }
  .nav-center {
    flex: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0;
  }

  .main-center h2 {
    font-size: 1.75rem;
  }
  .nav-number-center {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    /* width: 50px; */
    height: 50%;
    margin-right: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: yellow;
    padding: 0.5rem;
    font-size: 0.9rem;
  }
}
