.section::-webkit-scrollbar,
.menulist-container::-webkit-scrollbar {
  /* display: none; */
}
.section,
.menulist-container {
  /* -ms-overflow-style: none; */
  /* scrollbar-width: none; */
}

.section {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 0.2rem 0;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 0.25rem;
  overflow: hidden;
}
.menulist-container {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.menulist-center {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 250px;
  gap: 4px;
  padding: 0.125rem;
}

@media screen and (max-width: 479px) {
  .menulist-center {
    /* grid-template-columns: repeat(auto-fill, minmax(120px, 1fr)); */
    /* grid-auto-rows: 140px; */
    grid-template-columns: 1fr;
    grid-auto-rows: minmax(120px, 150px);
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .menulist-center {
    /* grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); */
    /* grid-auto-rows: 170px; */
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: minmax(150px, 180px);
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .menulist-center {
    /* grid-template-columns: repeat(auto-fill, minmax(180px, 1fr)); */
    /* grid-auto-rows: 200px; */
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(200px, 250px);
  }
}
@media screen and (min-width: 1024px) and (max-width: 1279px) {
  .menulist-center {
    /* grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); */
    /* grid-auto-rows: 240px; */
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(250px, 300px);
  }
}
@media screen and (min-width: 1280px) and (max-width: 1439px) {
  .menulist-center {
    /* grid-template-columns: repeat(auto-fill, minmax(220px, 1fr)); */
    /* grid-auto-rows: 270px; */
    grid-template-columns: repeat(5, 1fr);

    grid-auto-rows: minmax(300px, 350px);
  }
}
@media screen and (min-width: 1440px) {
  .menulist-center {
    /* grid-template-columns: repeat(auto-fill, minmax(250px, 1fr)); */
    /* grid-auto-rows: 300px; */
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: minmax(350px, 400px);
  }
}
